
header{
  
  height: 10vh;
  background-color: black;
  nav{
    color: white;
    font-size: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    a{color: white; text-decoration-line: none;}
  }
}
h2{
  margin: 0;
}
main{
  background-color: aqua;
  height: 100vh;
  .react_sample{
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .page2{
    h2{
      display: flex;
      justify-content: center;
      margin: 0;
    }
    font-size: 4rem;
  }
  .Profile{
    height: 90vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 25rem;
    
      div{
        width: 45%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        span{
          width: 100%;
          height: 50vh;
          clip-path: circle(25% at 50% 50%); 
          background-color: lightgray;
        }
      }
      p{font-size: 2rem;}
  }
}